'use client';

import { useState, useRef, useEffect } from 'react';
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Checkbox } from "@/components/ui/checkbox";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Leaf, Mic, Loader2, User, Mail, FingerprintIcon, Home, Map, Tractor, TreePine, Camera, Upload } from "lucide-react";
import { useToast } from "@/hooks/use-toast";
import { useLanguage } from '@/contexts/language-context';
import { Progress } from '@/components/ui/progress';
import { Alert, AlertTitle, AlertDescription } from '@/components/ui/alert';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';


const Step1 = ({ nextStep, t }: { nextStep: () => void, t: (key: string, section?: string) => string }) => {
    return (
        <div className="space-y-4">
            <div>
                <Label htmlFor="fullName">{t('fullName', 'register')}</Label>
                <Input id="fullName" placeholder={t('fullNamePlaceholder', 'register')} required />
            </div>
            <div>
                <Label htmlFor="phone">{t('phoneLabel', 'login')}</Label>
                <Input id="phone" type="tel" placeholder={t('phonePlaceholder', 'login')} required />
            </div>
            <div>
                <Label htmlFor="email">{t('email', 'register')}</Label>
                <Input id="email" type="email" placeholder={t('emailPlaceholder', 'register')} />
            </div>
            <div>
                <Label htmlFor="aadhaar">{t('aadhaarNumber', 'register')}</Label>
                <Input id="aadhaar" placeholder={t('aadhaarPlaceholder', 'register')} maxLength={12} />
            </div>
            <Button onClick={nextStep} className="w-full">{t('next', 'register')}</Button>
        </div>
    );
};


const Step2 = ({ nextStep, prevStep, t }: { nextStep: () => void, prevStep: () => void, t: (key: string, section?: string) => string }) => {
    const crops = [
        { value: 'paddy', label: t('cropPaddy', 'register') },
        { value: 'wheat', label: t('cropWheat', 'register') },
        { value: 'coconut', label: t('cropCoconut', 'register') },
        { value: 'vegetables', label: t('cropVegetables', 'register') },
        { value: 'rubber', label: t('cropRubber', 'register') },
        { value: 'sugarcane', label: t('cropSugarcane', 'register') },
        { value: 'cotton', label: t('cropCotton', 'register') },
    ];
    const [selectedCrops, setSelectedCrops] = useState<string[]>([]);

    const handleCropChange = (crop: string) => {
        setSelectedCrops(prev => 
            prev.includes(crop) ? prev.filter(c => c !== crop) : [...prev, crop]
        );
    }
    
    return (
        <div className="space-y-4">
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <div>
                    <Label>{t('state', 'register')}</Label>
                    <Select>
                        <SelectTrigger><SelectValue placeholder={t('selectState', 'register')} /></SelectTrigger>
                        <SelectContent><SelectItem value="kerala">Kerala</SelectItem></SelectContent>
                    </Select>
                </div>
                <div>
                    <Label>{t('district', 'register')}</Label>
                    <Select>
                        <SelectTrigger><SelectValue placeholder={t('selectDistrict', 'register')} /></SelectTrigger>
                        <SelectContent><SelectItem value="tvm">Thiruvananthapuram</SelectItem></SelectContent>
                    </Select>
                </div>
            </div>
            <div>
                <Label htmlFor="village">{t('village', 'register')}</Label>
                <Input id="village" placeholder={t('villagePlaceholder', 'register')} />
            </div>
            <div>
                <Label>{t('landType', 'register')}</Label>
                <Select>
                    <SelectTrigger><SelectValue placeholder={t('selectLandType', 'register')} /></SelectTrigger>
                    <SelectContent>
                        <SelectItem value="irrigated">{t('landTypeIrrigated', 'register')}</SelectItem>
                        <SelectItem value="rainfed">{t('landTypeRainfed', 'register')}</SelectItem>
                    </SelectContent>
                </Select>
            </div>
             <div>
                <Label htmlFor="landSize">{t('landSize', 'register')}</Label>
                <Input id="landSize" type="number" placeholder={t('landSizePlaceholder', 'register')} />
            </div>
            <div>
                <Label>{t('crops', 'register')}</Label>
                 <Popover>
                    <PopoverTrigger asChild>
                        <Button variant="outline" className="w-full justify-start font-normal">
                            {selectedCrops.length > 0 ? selectedCrops.map(c => crops.find(crop => crop.value === c)?.label).join(', ') : t('selectCrops', 'register')}
                        </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-full p-0">
                        <div className="space-y-2 p-2">
                           {crops.map(crop => (
                               <div key={crop.value} className="flex items-center space-x-2">
                                   <Checkbox 
                                       id={crop.value}
                                       checked={selectedCrops.includes(crop.value)}
                                       onCheckedChange={() => handleCropChange(crop.value)}
                                   />
                                   <Label htmlFor={crop.value}>{crop.label}</Label>
                               </div>
                           ))}
                        </div>
                    </PopoverContent>
                </Popover>
            </div>
            <div className="flex justify-between">
                <Button variant="outline" onClick={prevStep}>{t('back', 'register')}</Button>
                <Button onClick={nextStep}>{t('next', 'register')}</Button>
            </div>
        </div>
    );
};


const Step3 = ({ prevStep, t }: { prevStep: () => void, t: (key: string, section?: string) => string }) => {
    const { toast } = useToast();
    const videoRef = useRef<HTMLVideoElement>(null);
    const [hasCameraPermission, setHasCameraPermission] = useState<boolean | null>(null);
    const [capturedImage, setCapturedImage] = useState<string | null>(null);
    const [isSubmitting, setIsSubmitting] = useState(false);

    useEffect(() => {
        const getCameraPermission = async () => {
            try {
                const stream = await navigator.mediaDevices.getUserMedia({ video: true });
                setHasCameraPermission(true);
                if (videoRef.current) {
                    videoRef.current.srcObject = stream;
                }
            } catch (error) {
                console.error('Error accessing camera:', error);
                setHasCameraPermission(false);
                toast({
                    variant: 'destructive',
                    title: t('cameraError', 'register'),
                    description: t('cameraErrorDesc', 'register'),
                });
            }
        };
        getCameraPermission();
        
        return () => {
             if (videoRef.current && videoRef.current.srcObject) {
                const stream = videoRef.current.srcObject as MediaStream;
                stream.getTracks().forEach(track => track.stop());
            }
        }
    }, [t, toast]);

    const handleCapture = () => {
        if (videoRef.current) {
            const canvas = document.createElement('canvas');
            canvas.width = videoRef.current.videoWidth;
            canvas.height = videoRef.current.videoHeight;
            const context = canvas.getContext('2d');
            if (context) {
                context.drawImage(videoRef.current, 0, 0, canvas.width, canvas.height);
                setCapturedImage(canvas.toDataURL('image/jpeg'));
            }
        }
    };
    
    const handleSubmit = async () => {
        setIsSubmitting(true);
        // Mock submission
        await new Promise(res => setTimeout(res, 2000));
        setIsSubmitting(false);
        toast({ title: t('registrationSuccess', 'register') });
        window.location.href = '/login';
    }


    return (
        <div className="space-y-4">
            <div className="space-y-2">
                <Label>{t('uploadAadhaar', 'register')}</Label>
                <Input type="file" accept="application/pdf,image/*" />
            </div>
            <div className="space-y-2">
                <Label>{t('uploadLandDoc', 'register')}</Label>
                <Input type="file" accept="application/pdf,image/*" />
            </div>
            <div className="space-y-2">
                 <Label>{t('livePhoto', 'register')}</Label>
                 <div className="border rounded-md p-2 space-y-2">
                    <video ref={videoRef} className="w-full aspect-video rounded-md bg-muted" autoPlay muted playsInline />
                     {hasCameraPermission === false && (
                        <Alert variant="destructive">
                          <AlertTitle>{t('cameraAccessRequired', 'register')}</AlertTitle>
                          <AlertDescription>{t('cameraAccessRequiredDesc', 'register')}</AlertDescription>
                        </Alert>
                      )}
                      {capturedImage && <img src={capturedImage} alt="Captured" className="w-full rounded-md" />}
                     <Button onClick={handleCapture} disabled={!hasCameraPermission} type="button" className="w-full">
                         <Camera className="mr-2" /> {capturedImage ? t('retakePhoto', 'register') : t('capturePhoto', 'register')}
                    </Button>
                 </div>
            </div>
            <div className="flex justify-between">
                <Button variant="outline" onClick={prevStep} disabled={isSubmitting}>{t('back', 'register')}</Button>
                <Button onClick={handleSubmit} disabled={isSubmitting}>
                    {isSubmitting && <Loader2 className="animate-spin mr-2" />}
                    {t('submit', 'register')}
                </Button>
            </div>
        </div>
    );
};


export default function RegisterPage() {
    const { t } = useLanguage();
    const [step, setStep] = useState(1);

    const nextStep = () => setStep(s => s + 1);
    const prevStep = () => setStep(s => s - 1);
    
    const progress = (step / 3) * 100;
    const stepTitles = [t('step1Title', 'register'), t('step2Title', 'register'), t('step3Title', 'register')];

    return (
        <Card className="w-full max-w-lg mx-auto bg-card/90 backdrop-blur-sm">
            <CardHeader>
                <div className="text-center mb-4">
                     <Leaf className="h-10 w-10 text-primary mx-auto" />
                     <h1 className="text-2xl font-bold">{t('createAccount', 'register')}</h1>
                </div>
                <Progress value={progress} className="w-full" />
                <p className="text-center text-sm font-medium mt-2">{t('step', 'register')} {step}/3: {stepTitles[step-1]}</p>
            </CardHeader>
            <CardContent>
                {step === 1 && <Step1 nextStep={nextStep} t={t} />}
                {step === 2 && <Step2 nextStep={nextStep} prevStep={prevStep} t={t} />}
                {step === 3 && <Step3 prevStep={prevStep} t={t} />}
            </CardContent>
        </Card>
    );
}
